.dashboardPageContainer {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-auto-rows: auto;
  padding: 10px;
}

.linkStyle {
  padding: 10px;
}

@media (max-width: 900px) {
  .dashboardPageContainer {
    display: grid;
    grid-template-columns: 1fr;
    justify-items: center;
  }
}
